import * as React from "react"
import { HeadFC, Link, PageProps, navigate } from "gatsby"
import AppContent from "../components/AppContent"
import Head_ from "../components/Head_"
import Button_ from "../components/Button_"

const NotFoundPage: React.FC<PageProps> = () => {
  const [hitokoto, setHitokoto] = React.useState({ text: '', to: '' })
  React.useEffect(() => {
    fetch('https://v1.hitokoto.cn')
      .then(response => response.json())
      .then(data => {
        setHitokoto({ to: `https://hitokoto.cn/?uuid=${data.uuid}`, text: data.hitokoto })
      })
      .catch(console.error)
  }, [])
  return (
    <AppContent setTheme={undefined}>
      <div className="flex flex-col items-center gap-3">
      <div className="text-4xl text-center dark:text-gray-50">空页面</div>
      <div className="text-xl text-center bg-gray-100 dark:bg-gray-700 rounded-md max-w-[calc(100vw-30px)] overflow-hidden p-4 dark:text-gray-50">{hitokoto.text}&nbsp;&nbsp;&nbsp;<Link className="underline" to={hitokoto.to}>源于hitokoto</Link></div>
      <Button_ onClick={()=>{navigate("/")}}>回到首页</Button_>
      </div>
    </AppContent>
  )
}

export default NotFoundPage

export const Head: HeadFC = () => <><title>空页面</title><Head_ /><script dangerouslySetInnerHTML={{ __html: `if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}` }}></script></>
